import React from "react";

export default class NewsLater extends React.Component {
  render() {
    return (
      <div className="">
        <div className="displayheading">
          <h3>Subscribe to our Newslater</h3>
        </div>

        <div className="row">
          <div className="col-md-4">
            <label style={{ color: "black", fontSize: ".9em" }} className="lab">
              Your Name (Required)
            </label>
            <input className="inp" />
          </div>
          <div className="col-md-4">
            <label style={{ color: "black", fontSize: ".9em" }} className="lab">
              Your Email (Required)
            </label>
            <input className="inp" />
          </div>
          <div className="col-md-4">
            <label style={{ color: "black", fontSize: ".9em" }} className="lab">
              Phone Number (Required)
            </label>
            <input className="inp" />
          </div>
          <div className="col-md-4">
            <button
              style={{ backgroundColor: "#084E97", margin: "10px 0" }}
              className="btntop"
            >
              Send
            </button>
          </div>
        </div>
      </div>
    );
  }
}
